<script>
import AppTitle    from '@/components/shared/Title'

import energySankey from '@/components/subs/energySankey'
import waterSankey from '@/components/subs/waterSankey'
// import color from '@/components/plugins/color'

export default{
  components: {energySankey, waterSankey},

  data: () => ({
    solarTrajectoryItems: [{ text: 'Uniform', value: 0 }, { text: 'Radial', value: 1 }],
    dayNightItems: [{ text: 'Day', value: 0 }, { text: 'Night', value: 1 }],
    weatherItems: [{ text: 'Shiny', value: 0 }, { text: 'Cloudy', value: 1 }, { text: 'Rainy', value: 2 }],
    bldgImgSrc: require('../assets/LBC1.png'),
    bldgImgSrc2: require('../assets/LBC2.jpg'),
    waterCollectionRate: 0, //gpm
    waterConsumptionRate: 5, //gpm
    elecConsumptionRate: 90, //kW
    LBCImage1: null,
    loading: false,
    search: null,
    select: null,
    waterChart: null,
    sunChart: null,
    sunChart2: null,
    weatherChart: null,
    weatherChart2: null,
    weatherChartOption: null,
    weatherChartOption2: null,
    waterLevelPerc: 50,
    solarPower: 8,
    starPath: 'path://m 77.107143,118.59523 0.75595,-32.883922 18.520837,26.080362 24.56845,-23.812504 -4.53572,32.505944 31.08854,-3.21279 -24.47395,20.97767 24.75744,24.94643 -34.01786,-4.15774 0.37798,34.39584 -21.166668,-27.21429 -24.568454,22.3006 5.669644,-31.37202 -30.238095,2.64583 23.434524,-21.16667 -22.67857,-24.19047 z',
    solarTrajectoryOption: 0,
    solarTrajectory: [[{coords: [[3,10],[4,8.2]]},{coords: [[4,10],[5,8.2]]},{coords: [[5,10],[6,8.2]]},{coords: [[6,10],[7,8.2]]},{coords: [[7,10],[8,8.2]]},{coords: [[8,10],[9,8.2]]},{coords: [[9,10],[10,8.2]]}],
    [{coords: [[5,10],[4,8.2]]},{coords: [[5,10],[5,8.2]]},{coords: [[5,10],[6,8.2]]},{coords: [[5,10],[7,8.2]]},{coords: [[5,10],[8,8.2]]},{coords: [[5,10],[9,8.2]]},{coords: [[5,10],[10,8.2]]}]],
    weatherOption: 0,
    dayNightOption: 0,
    weatherColors: [
      ['background-color:rgba(255,185,0,0.9)','background-color:rgba(20,20,155,1)','background-color:rgba(20,20,155,1)'],
      ['background-color:rgba(0,0,0,.9)','background-color:rgba(0,0,40,.9)','background-color:rgba(0,0,40,.9)'],
    ],
    dayNightOpacity: ['opacity:0.8','opacity:0.4'],
    rainTrajectory1a: [],
    rainTrajectory1b: [],
    rainTrajectory2a: [],
    rainTrajectory2b: [],
    rainTrajectory3a: [],
    rainTrajectory3b: [],


  }),
  methods: {
    zoomOut() {
      this.waterLevelPerc = _.max([this.waterLevelPerc - 10, 0])
    },
    zoomIn() {
      this.waterLevelPerc = _.min([this.waterLevelPerc + 10, 100])
    },
    zoomOutSolar() {
      this.solarPower = _.max([this.solarPower - 1, 0])
    },
    zoomInSolar() {
      this.solarPower = _.min([this.solarPower + 1, 9])
    },
    refreshPowerSankey() {

    },
    navigate(item,index) {
      // console.log('navigating...')
      // console.log(router.currentRoute)
      router.push({name: item});
    },
    refreshWaterChart() {
      this.waterChart.setOption(this.waterChartOption)
      this.waterChart.resize()
    },
    refreshSunChart() {
      this.sunChart.setOption(this.sunChartOption)
      this.sunChart.resize()

      this.sunChart2.setOption(this.sunChartOption)
      this.sunChart2.resize()
    },
    getRainData() {
      let rainData = [];
      for(let i = 0; i < 30; i++) {
        let x = Math.random()*1.3;
        rainData.push({coords: [[x,10],[x+0.2,0]]})
      };
      this.rainTrajectory1a = rainData.splice(0,rainData.length/2);
      this.rainTrajectory1b = rainData.splice(rainData.length/2,rainData.length);
      rainData = [];
      for(let i = 0; i < 50; i++) {
        let x = Math.random()*2.5+1.3
        rainData.push({coords: [[x,10],[x+0.2,5]]})
      };
      this.rainTrajectory2a = rainData.splice(0,rainData.length/2);
      this.rainTrajectory2b = rainData.splice(rainData.length/2,rainData.length);
      rainData = [];
      for(let i = 0; i < 90; i++) {
        let x = Math.random()*6.5+3.8
        rainData.push({coords: [[x,10],[x+0.2,8.2]]})
      };
      this.rainTrajectory3a = rainData.splice(0,rainData.length/2);
      this.rainTrajectory3b = rainData.splice(rainData.length/2,rainData.length);
    },
    refreshWeatherChart() {
      if(this.weatherOption == 0) {
        this.waterCollectionRate = 0; //gpm
        this.solarPower = 8
        if(this.weatherChartOption !== null) {
          this.weatherChartOption.series = []
          this.weatherChart.setOption(this.weatherChartOption)
          this.weatherChart2.setOption(this.weatherChartOption)
        }
      }
      else if(this.weatherOption == 1) {
        this.waterCollectionRate = 0 //gpm
        this.solarPower = 4
        if(this.weatherChartOption !== null) {
          this.weatherChartOption.series = []
          this.weatherChart.setOption(this.weatherChartOption)
          this.weatherChart2.setOption(this.weatherChartOption)
        }
      } else if(this.weatherOption == 2) {
        this.waterCollectionRate = 10; //gpm
        this.solarPower = 1
        this.weatherChartOption = {
          grid: {
            top: 0,
            left: 0,
          },
          xAxis: {
            type: 'value',
            show: false,
            min: 0,
            max: 10,
          },
          yAxis: {
            min: 0,
            max: 10,
            show: false,
          },
          series: [
            {
              name: 'tail', type: 'lines', coordinateSystem: 'cartesian2d', zlevel: 1,
              effect: {
                show: true, constantSpeed: 40, trailLength: .3, color: 'blue', symbolSize: 1
              },
              lineStyle: {
                normal: {
                  color: 'blue', width: 0, curveness: 0
                }
              },
              data: this.rainTrajectory1a
            },
            {
              name: 'tail', type: 'lines', coordinateSystem: 'cartesian2d', zlevel: 1,
              effect: {
                show: true, constantSpeed: 60, trailLength: .3, color: 'blue', symbolSize: 1
              },
              lineStyle: {
                normal: {
                  color: 'blue', width: 0, curveness: 0
                }
              },
              data: this.rainTrajectory1b
            },
            {
              name: 'tail', type: 'lines', coordinateSystem: 'cartesian2d', zlevel: 1,
              effect: {
                show: true, constantSpeed: 40, trailLength: .3, color: 'blue', symbolSize: 1
              },
              lineStyle: {
                normal: {
                  color: 'blue', width: 0, curveness: 0
                }
              },
              data: this.rainTrajectory2a
            },
            {
              name: 'tail', type: 'lines', coordinateSystem: 'cartesian2d', zlevel: 1,
              effect: {
                show: true, constantSpeed: 60, trailLength: .3, color: 'blue', symbolSize: 1
              },
              lineStyle: {
                normal: {
                  color: 'blue', width: 0, curveness: 0
                }
              },
              data: this.rainTrajectory2b
            },
            {
              name: 'tail', type: 'lines', coordinateSystem: 'cartesian2d', zlevel: 1,
              effect: {
                show: true, constantSpeed: 40, trailLength: .3, color: 'blue', symbolSize: 1
              },
              lineStyle: {
                normal: {
                  color: 'blue', width: 0, curveness: 0
                }
              },
              data: this.rainTrajectory3a
            },
            {
              name: 'tail', type: 'lines', coordinateSystem: 'cartesian2d', zlevel: 1,
              effect: {
                show: true, constantSpeed: 60, trailLength: .3, color: 'blue', symbolSize: 1
              },
              lineStyle: {
                normal: {
                  color: 'blue', width: 0, curveness: 0
                }
              },
              data: this.rainTrajectory3b
            },
          ],
        }
        this.weatherChart.setOption(this.weatherChartOption)
        this.weatherChart2.setOption(this.weatherChartOption)
        this.weatherChart.resize()
        this.weatherChart2.resize()
      }

      if(!this.dayNightOption) return this.refreshSunChart()
      this.solarPower = 0
      const sunChartOption = this.sunChart.getOption()
      sunChartOption.series = []
      this.sunChart.setOption(sunChartOption, true)
      this.sunChart2.setOption(sunChartOption, true)

    },
    open (event) {
      alert(event.title)
    },
    onResize() {
      if(this.waterChart)   _.debounce(this.waterChart.resize, 100)()
      if(this.sunChart)     _.debounce(this.sunChart.resize, 100)()
      if(this.weatherChart) _.debounce(this.weatherChart.resize, 100)()
    },
  },
  watch: {
    waterChartOption(to) {
      this.refreshWaterChart()
    },
    sunChartOption(to) {
      this.refreshSunChart()
    }
  },
  computed: {
    waterChartOption() {
      return {
        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
            offset: 0,
            color: 'white'
        }, {
            offset: 1,
            color: 'rgb(0, 149, 204)'
        }]),
        grid: {
          bottom: 0,
          left: 0,
          top: 0,
          right: 0,
        },
        xAxis: {
          data: ['Water'],
          show: false,
        },
        yAxis: {
          show: false,
          min: 0,
          max: 100,
        },
        series: [{
          type: 'bar',
          data: [this.waterLevelPerc],
        }],
      }
    },
    sunChartOption() {
      return {
        grid: {
          top: 5,
        },
        xAxis: {
          type: 'value',
          show: false,
          min: 0,
          max: 10,
        },
        yAxis: {
          min: 0,
          max: 10,
          show: false,
        },
        series: [{
          name: 'tail',
          type: 'lines',
          coordinateSystem: 'cartesian2d',
          zlevel: 1,
          effect: {
            show: true,
            period: 10-this.solarPower,
            trailLength: .8,
            color: '#ffa022',
            symbolSize: 3
          },
          lineStyle: {
            normal: {
              color: '#ffa022',
              width: 0,
              curveness: 0.05
            }
          },
          data: this.solarTrajectory[this.solarTrajectoryOption]
        }, {
          name: 'sun',
          type: 'lines',
          coordinateSystem: 'cartesian2d',
          zlevel: 2,
          //symbol: ['none', 'path://m 27.553357,212.43475 77.379793,-63.40918 -17.160941,-4.09821 90.815721,-72.69271 -73.03569,68.18926 16.94412,4.33925 z'],
          symbolSize: 25,
          effect: {
            show: true,
            period: 10-this.solarPower,
            trailLength: 0,
            symbol: this.starPath,
            symbolSize: 15
          },
          lineStyle: {
            normal: {
              color: '#ffa022',
              width: 0,
              opacity: 0.6,
              curveness: 0.05
            }
          },
          data: this.solarTrajectory[this.solarTrajectoryOption]
        }]
      }
    }
  },
  mounted() {
    this.sunChart     = echarts.init(this.$refs.sunEcharts)
    this.waterChart   = echarts.init(this.$refs.waterEcharts)
    this.weatherChart = echarts.init(this.$refs.weatherEcharts)

    this.sunChart2     = echarts.init(this.$refs.sunEcharts2)
    this.weatherChart2 = echarts.init(this.$refs.weatherEcharts2)

    this.getRainData()
    this.refreshWaterChart()
    this.refreshSunChart()
    this.refreshWeatherChart()
  },
  beforeDestroy() { //** garbage collection for performance
    echarts.dispose(this.waterChart)
    echarts.dispose(this.sunChart)
    echarts.dispose(this.weatherChart)
    echarts.dispose(this.sunChart2)
    echarts.dispose(this.weatherChart2)
  },
  components: {
    AppTitle
  }
}
</script>
